import Vue from 'vue'
import Router from 'vue-router'
import index from '@/views/index'
import about from '@/views/about'
import contact from '@/views/contact'
import cases from '@/views/cases'
import solutions from '@/views/solutions'
import culture from '@/views/culture'
import structure from '@/views/structure'
import certificate from '@/views/certificate'
import partners from '@/views/partners'
import fwkh from '@/views/fwkh'
import info from '@/views/info'
import jjcg from '@/views/jjcg'


Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            name: 'index',
            component: index
        },
        {
            path: '/index',
            name: 'index',
            component: index
        },
        {
            path: '/info',
            name: 'info',
            component: info,
            children: [{
                    path: '/',
                    redirect: "about"
                },
                {
                    path: 'partners',
                    "name": "合作伙伴",
                    component: partners
                }, {
                    path: 'fwkh',
                    "name": "服务客户",
                    component: fwkh
                }, {
                    path: 'certificate',
                    "name": "资质证书",
                    component: certificate
                }, {
                    path: 'structure',
                    "name": "组织结构",
                    component: structure
                },
                {
                    path: 'culture',
                    "name": "企业文化",
                    component: culture
                },
                {
                    path: 'about',
                    "name": "关于斗文",
                    component: about
                }
            ]
        }, {
            path: '/contact',
            name: 'contact',
            component: contact
        }, {
            path: '/jjcg',
            name: 'jjcg',
            component: jjcg,
            children: [{
                    path: '/',
                    redirect: "cases"
                },
                {
                    path: 'cases',
                    name: '成功案例',
                    component: cases
                }, {
                    path: 'solutions',
                    name: '解决方案',
                    component: solutions
                }
            ]
        }
    ]
})


